import {uniCollapse,uniCollapseItem} from "@dcloudio/uni-ui"
import uniDrawer from "@/components/uni-drawer/uni-drawer.vue"


export default {
	data() {
		return {
			showDrawer:false
			
		}
	},
	methods: {
		openMenu()
		{
			this.showDrawer = true;
		},
		fuck()
		{
			this.showDrawer = false;
		},
		addStudent()
		{
			uni.navigateTo({
				url:"../recruit/add2/add2"
			})
		},
		myStudentRouter()
		{
			this.showDrawer = false;
			uni.navigateTo({
				url: "../recruit/consultation/router/router"
			})
		},
		//我的回访
		myReturnVisitRouter()
		{
			this.showDrawer = false;
			uni.navigateTo({
				url:"../recruit/consultation/visit_router/visit_router"
			})
		},
		//回访提醒
		returnVisitToRemind()
		{
			this.showDrawer = false;
			uni.navigateTo({
				url:"../recruit/consultation/visit_remind_router/visit_remind_router"
			})
		},
		//生日提醒
		birthdayReminder()
		{
			this.showDrawer = false;
			uni.navigateTo({
				url:"../recruit/consultation/birthday_reminder_router/birthday_reminder_router"
			})
		},
		//学校调研
		surveySchool()
		{
			this.showDrawer = false;
			uni.navigateTo({
				url:"../recruit/survey/survey_school/survey_school"
			})
			
			
		},
		ledgerSchool()
		{
			this.showDrawer = false;
			uni.navigateTo({
				url: "../recruit/ledger/ledger_school/ledger_school"
			})
		}
	},
	components:{
		uniCollapse,uniCollapseItem,uniDrawer
	},
	onLoad() {
		var me = this
		uni.request({
			url: me.serverUrl + '/login/status',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res.data)
				if(res.data.status==200)
				{
					// 仍旧是登录状态
				
				}else
				{
					// me.login = false;
					// uni.removeStorageSync('userInfo')
					var username = uni.getStorageSync("username")
					var password = uni.getStorageSync("password")
					if(username!=null & password !=null)
					{
						uni.request({
							url: me.serverUrl + '/login/mobile' ,
							method: 'POST',
							header:{
								"content-type":'application/x-www-form-urlencoded'
							},
							data: {
								username:username,
								password:password
							},
							success: res => {
								me.loadModal = false
								if(res.statusCode==404)
								{
									uni.showToast({
										title: "服务器超时",
										duration:2000
									})
								}
								
								
								if(res.data.status==200)
								{
									//登录成功
									uni.setStorageSync('userInfo',res.data.data)
									
								}else
								{
									
									uni.showToast({
										title:res.data.msg,
										duration: 2000
									})
								}
								
								console.log(res.data)
							},
							fail: () => {
								console.log("出错了")
							},
							complete: () => {}
						});
					}
					
					
				}
				
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
